import "./App.css";
import xlsxParser from "xls-parser";
import { useEffect, useRef, useState } from "react";
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Select from 'react-select'
import Toast from 'react-bootstrap/Toast';
import ToastContainer from 'react-bootstrap/ToastContainer';
import { TreeTable, TreeState } from 'cp-react-tree-table';

const COLUMN_NAME = {
  user: ['', 'User'],
  role: ['', 'Role'],
  project: ['', 'Project'],
  time: ['Logged', ' '],
}

export const Main = () => {

    const [state, setState] = useState({
        users: [],
        tree: {},
        report: []
      });
    
      const [showToast, setShowToast] = useState(false);
      const [showToast2, setShowToast2] = useState(false);
      const [toastText, setToastText] = useState('');
      const [position, _] = useState('bottom-end');
    
      const fileRef = useRef(null);
    
      const getByKeys = (obj, keys) => {
        for (let i = 0; i < keys.length; i++) {
          if (obj[keys[i]]) return obj[keys[i]]
        }
    
        return ''
      }
    
      useEffect(() => {
        fileRef.current.onchange = () => {
          const selectedFile = fileRef.current.files[0];
          setToastText(' ')
          setShowToast(true)
    
          try {
            
          xlsxParser.onFileSelection(selectedFile, { showNullProperties: true, hideEmptyRows: false }).then((data) => {
            const users = new Set()
    
            data[Object.keys(data)[0]]
              .filter(el => getByKeys(el, COLUMN_NAME.user) && getByKeys(el, COLUMN_NAME.role) === 'Project Manager')
              .forEach(el => users.add(getByKeys(el, COLUMN_NAME.user)))
    
            const obj = data[Object.keys(data)[0]]
              .filter(el => getByKeys(el, COLUMN_NAME.user) && getByKeys(el, COLUMN_NAME.role) === 'Project Manager')
              .reduce((acc, el) => {
                if (acc[getByKeys(el, COLUMN_NAME.user)]) {
                  acc[getByKeys(el, COLUMN_NAME.user)].push({ project: getByKeys(el, COLUMN_NAME.project), allTime: 0, teamTime: 0, pmTime: 0, roles: [] })
    
                } else {
                  acc[getByKeys(el, COLUMN_NAME.user)] = [{ project: getByKeys(el, COLUMN_NAME.project), allTime: 0, teamTime: 0, pmTime: 0, roles: [] }]
                }
                return acc
              }, {})
    
            const report = data[Object.keys(data)[0]]
              .filter(row => getByKeys(row, COLUMN_NAME.role))
              .filter(row => Object.values(obj).find(pmInfo => pmInfo.find(info => info.project === getByKeys(row, COLUMN_NAME.project))))
              .reduce((acc, row) => {
                const project = getByKeys(row, COLUMN_NAME.project);
                const name = getByKeys(row, COLUMN_NAME.user);
                const time = getByKeys(row, COLUMN_NAME.time);
                const role = getByKeys(row, COLUMN_NAME.role);
    
                console.log(acc, name, project)
                if (acc[name]) {
                  let projIndex = acc[name].findIndex(pmInfo => pmInfo.project === project)
    
                  acc[name][projIndex] = {
                    ...acc[name][projIndex],
                    allTime: summTime(acc[name][projIndex].allTime, time),
                    pmTime: +time,
                  }
    
                } else {
                  const pm = Object
                    .keys(acc)
                    .find(pmName => acc[pmName].find(info => info.project === project))
                    console.log(acc, pm)
                  const proj = acc[pm].find(pmInfo => pmInfo.project === project)
                  proj.allTime = summTime(proj.allTime, time)
                  proj.teamTime = summTime(proj.teamTime, time)
    
                  const existRole = proj.roles.find(roleInfo => roleInfo.role === role)
    
                  if (existRole) {
                    existRole.time = summTime(existRole.time, time)
                    existRole.users.push({ name, time: summTime(time, 0) })
                  } else {
                    proj.roles.push({ role, time: summTime(time, 0), users: [{ name, time: summTime(time, 0) }] })
                  }
                }
    
                return acc
              }, obj)
    
            if(!users.size) {
              setShowToast2(true)
            }
    
            setState({
              ...state,
              file: data,
              report,
              users: [...users].map(el => ({ value: el, label: el }))
            });
          }).catch(e => {
            console.log(e)
            setToastText(' ')
            setShowToast(true)
          })
    
        } catch (error) {
          setToastText(' ')
          setShowToast(true)
        }
        };
      }, []);
    
      const summTime = (t1, t2) => {
        return ((+t1 * 100 + t2 * 100) / 100).toFixed(2);
      }
    
      const addToTable = (_, event) => {
        if (event.action === 'remove-value') {
          const newTree = state.tree;
    
          delete newTree[event.removedValue.value]
    
          setState({ ...state, tree: { ...newTree }})
        } else if (event.action === 'clear') {
          setState({ ...state, tree: {} })
        } else {
            const treeValue = TreeState.expandAll(TreeState.create(
              state.report[event.option.value]
              .map((project) => {
                return {
                  data: {
                    project: project.project,
                    userTime: project.pmTime,
                    otherTime: project.teamTime,
                    allTime: project.allTime,
                  },
                  children: project.roles.map(role => {
                    return {
                      data: {
                        project: role.role || '-',
                        userTime: role.time,
                        otherTime: '-',
                        allTime: '-',
                      },
                      children: role.users.map(user => {
                        return {
                          data: {
                            project: user.name,
                            userTime: user.time,
                            otherTime: '-',
                            allTime: '-',
                          }
                        }
                      })
                    }
                  })
                }
              })))
    
              setState({ ...state, tree: { ...state.tree, [event.option.value]: treeValue }})
        }
      }
    
      const addFileWithReport = () => {
        fileRef.current.click();
      }
    
      const handleOnChange = (newValue, userName) => {
        setState({ ...state, tree: { ...state.tree, [userName]: newValue }});
      }

    return <div className="App position-relative" aria-live="polite"
    aria-atomic="true">
      <input ref={fileRef} type="file" accept=".xls, .csv, .xlsx" hidden/>

      <div className="cont">
        {state.users.length > 0 && <Select
          styles={{ control: (styles) => ({ ...styles, minWidth: 200 }), }}
          isMulti
          options={state.users}
          closeMenuOnSelect={false}
          className="basic-multi-select"
          isClearable={true}
          placeholder=" "
          onChange={addToTable}
        />}

        <Button variant="primary" style={{margin: 10}} onClick={() => addFileWithReport()}>   </Button>{' '}
      </div>

      <div className="card-list">
        {Object.entries(state.tree).map(([pmName, pmInfo]) => {
          return <Card >
          <Card.Body>
            <Card.Title>{pmName}</Card.Title>

            <TreeTable
              onChange={(e) => handleOnChange(e, pmName)}
              value={pmInfo}>

              <TreeTable.Column
                renderCell={row => {
                  let className = 'line';

                  if (row.metadata.hasChildren) {
                    className+=' with-children'
                  } else {
                    className+=' without-children'
                  }

                  if (row.metadata.depth === 1) {
                    className+=' role-line'
                  } else if (row.metadata.depth === 2) {
                    className+=' user-line'
                  } else {
                    className+=' project-line'
                  }

                  return <div style={{ marginLeft: (row.metadata.depth * 15) + 'px'}}
                    className={className}>

                    {(row.metadata.hasChildren)
                      ? (
                          <button className="toggle-button" onClick={row.toggleChildren}></button>
                        )
                      : ''
                    }
                    
                    <span>{row.data.project}</span>
                  </div>
                }}
                renderHeaderCell={() => <span> </span>}
              />

              <TreeTable.Column
                renderCell={row => {
                  let className = 'line';

                  if (row.metadata.depth === 1) {
                    className+=' role-line'
                  } else if (row.metadata.depth === 2) {
                    className+=' user-line'
                  } else {
                    className+=' project-line'
                  }
                
                  return <div className={className}><span>{row.data.userTime}</span></div>
              }}
                renderHeaderCell={() => <span> </span>}
              />

              <TreeTable.Column
                renderCell={row => {
                  let className = 'line';

                  if (row.metadata.depth === 1) {
                    className+=' role-line'
                  } else if (row.metadata.depth === 2) {
                    className+=' user-line'
                  } else {
                    className+=' project-line'
                  }

                  return <div className={className}><span>{row.data.otherTime}</span></div>
                }}
                renderHeaderCell={() => <span>  </span>}
              />


              <TreeTable.Column
                renderCell={row => {
                  let className = 'line';

                  if (row.metadata.depth === 1) {
                    className+=' role-line'
                  } else if (row.metadata.depth === 2) {
                    className+=' user-line'
                  } else {
                    className+=' project-line'
                  }

                  return <div className={className}><span>{row.data.allTime}</span></div>
                }}
                renderHeaderCell={() => <span> </span>}
              />

            </TreeTable>
          </Card.Body>
        </Card>
        })}
      </div>

      <ToastContainer position={position}>
        <Toast show={showToast} delay={3000} onClose={() => setShowToast(false)} autohide>
          <Toast.Header>
            <strong className="me-auto"> </strong>
          </Toast.Header>
          <Toast.Body>{toastText}</Toast.Body>
        </Toast>

        <Toast show={showToast2} delay={3000} onClose={() => setShowToast2(false)} autohide>
          <Toast.Header>
            <strong className="me-auto"> </strong>
          </Toast.Header>
          <Toast.Body>     ""</Toast.Body>
        </Toast>
      </ToastContainer>
    </div>
}